/* src/pages/ForgotPasswordPage.css */

.forgot-password-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #f0f2f5;
    padding: 20px;
  }
  
  .forgot-password-page h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .forgot-password-page form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .forgot-password-page label {
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  .forgot-password-page input {
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .forgot-password-page button {
    padding: 0.75rem;
    border: none;
    border-radius: 4px;
    background: #007bff;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .forgot-password-page button:hover {
    background: #0056b3;
  }
  
  .forgot-password-page p {
    margin-top: 1rem;
    color: #28a745;
    font-weight: bold;
  }
  